import React, { useEffect, useCallback, useState, useMemo } from 'react'
import { Route, useRouteMatch, useLocation } from 'react-router-dom'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { Image, Heading, RowType, Toggle, Text } from 'uikit'
import styled from 'styled-components'
import FlexLayout from 'components/Layout/Flex'
import Page from 'components/Layout/Page'
import { BLOCKS_PER_YEAR } from 'config'
import { QuoteToken } from 'config/constants/types'
import { Vault } from 'state/types'
import { useTranslation } from 'contexts/Localization'
import { useAppDispatch } from 'state'
import useRefresh from 'hooks/useRefresh'
import { fetchVaultUserDataAsync } from 'state/vaults'
import { orderBy } from 'lodash'
import isArchivedPid from 'utils/vaultHelpers'
import { latinise } from 'utils/latinise'
import { useUserVaultStakedOnly } from 'state/user/hooks'
import { useVaults, usePriceDogeUSDC, usePriceYodeDoge } from 'state/hooks'
import { ViewMode } from 'state/user/actions'
import PageHeader from 'components/PageHeader'
import SearchInput from 'components/SearchInput'
import Select, { OptionProps } from 'components/Select/Select'
import VaultCard, { VaultWithStakedValue } from './components/VaultCard/VaultCard'
import Table from './components/VaultTable/VaultTable'
import VaultTabButtons from './components/VaultTabButtons'
import { RowProps } from './components/VaultTable/Row'
import { DesktopVaultColumnSchema } from './components/types'

const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;

  justify-content: space-between;
  flex-direction: column;
  margin-bottom: 32px;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 32px;
    margin-bottom: 0;
  }
`

const ToggleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;

  ${Text} {
    margin-left: 8px;
  }
`

const LabelWrapper = styled.div`
  > ${Text} {
    font-size: 12px;
  }
`

const FilterContainer = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  padding: 8px 0px;

  ${({ theme }) => theme.mediaQueries.sm} {
    width: auto;
    padding: 0;
  }
`

const ViewControls = styled.div`
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;

  > div {
    padding: 8px 0px;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    justify-content: flex-start;
    width: auto;

    > div {
      padding: 0;
    }
  }
`

const StyledImage = styled(Image)`
  margin-left: auto;
  margin-right: auto;
  margin-top: 58px;
`
const getDisplayApr = (cakeRewardsApr?: number) => {
  if (cakeRewardsApr) {
    return cakeRewardsApr.toLocaleString('en-US', { maximumFractionDigits: 2 })
  }
  return null
}

const Vaults: React.FC = () => {
  const { path } = useRouteMatch()
  const { pathname } = useLocation()
  const { t } = useTranslation()
  const vaultsLP = useVaults()
  const cakePrice = usePriceYodeDoge()
  const dogePrice = usePriceDogeUSDC()
  const [query, setQuery] = useState('')
  const viewMode = ViewMode.TABLE
  const { account } = useWeb3React()
  const [sortOption, setSortOption] = useState('hot')
  const { fastRefresh } = useRefresh()
  const dispatch = useAppDispatch()

  const isArchived = pathname.includes('archived')
  const isInactive = pathname.includes('history')
  const isActive = !isInactive && !isArchived

  useEffect(() => {
    if (account) {
      dispatch(fetchVaultUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  // Users with no wallet connected should see 0 as Earned amount
  // Connected users should see loading indicator until first userData has loaded

  const [stakedOnly, setStakedOnly] = useUserVaultStakedOnly(isActive)

  const activeVaults = vaultsLP.filter(
    (vault) => vault.pid !== 999 && vault.multiplier !== '0X' && !isArchivedPid(vault.pid),
  )
  const inactiveVaults = vaultsLP.filter(
    (vault) => vault.pid !== 999 && vault.multiplier === '0X' && !isArchivedPid(vault.pid),
  )
  const archivedVaults = vaultsLP.filter((vault) => isArchivedPid(vault.pid))

  const userDataReady = !account || activeVaults.length > 0

  const stakedOnlyVaults = activeVaults.filter(
    (vault) => vault.userData && new BigNumber(vault.userData.stakedBalance).isGreaterThan(0),
  )

  const stakedInactiveVaults = inactiveVaults.filter(
    (vault) => vault.userData && new BigNumber(vault.userData.stakedBalance).isGreaterThan(0),
  )

  const stakedArchivedVaults = archivedVaults.filter(
    (vault) => vault.userData && new BigNumber(vault.userData.stakedBalance).isGreaterThan(0),
  )

  const vaultsList = useCallback(
    (vaultsToDisplay: Vault[]): VaultWithStakedValue[] => {
      let vaultsToDisplayWithAPR: VaultWithStakedValue[] = vaultsToDisplay.map((vault) => {
        const cakeRewardPerBlock = new BigNumber(vault.mariachiPerBlock || 1)
          .times(new BigNumber(vault.poolWeight))
          .div(new BigNumber(10).pow(18))
        const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR)
        let apy = cakePrice.times(cakeRewardPerYear)
        let dailyApr = new BigNumber(0)
        let totalValue
        totalValue = new BigNumber(vault.lpTotalInQuoteToken || 0)

        if (vault.quoteTokenSymbol === QuoteToken.DOGE) {
          totalValue = totalValue.times(dogePrice)
        } else if (vault.quoteTokenSymbol === QuoteToken.YODE) {
          totalValue = totalValue.times(cakePrice)
        }
        if (totalValue.comparedTo(0) > 0) {
          apy = apy.div(totalValue)
          apy = apy.times(100)
          apy = apy.div(365)
          dailyApr = apy
          apy = apy.div(100)
          apy = apy.plus(1)
          apy = apy.pow(365)
          apy = apy.minus(1)
          apy = apy.times(100)
        }

        return { ...vault, apr: parseFloat(apy.toString()), dailyApr: parseFloat(dailyApr.toString()),  liquidity: totalValue }
      })

      if (query) {
        const lowercaseQuery = latinise(query.toLowerCase())
        vaultsToDisplayWithAPR = vaultsToDisplayWithAPR.filter((vault: VaultWithStakedValue) => {
          return latinise(vault.lpSymbol.toLowerCase()).includes(lowercaseQuery)
        })
      }
      return vaultsToDisplayWithAPR
    },
    [cakePrice, dogePrice, query],
  )

  const handleChangeQuery = (event: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(event.target.value)
  }

  const chosenVaultsMemoized = useMemo(() => {
    let chosenVaults = []

    const sortVaults = (vaults: VaultWithStakedValue[]): VaultWithStakedValue[] => {
      switch (sortOption) {
        case 'apr':
          return orderBy(vaults, (vault: VaultWithStakedValue) => vault.apr, 'desc')
        case 'liquidity':
          return orderBy(vaults, (vault: VaultWithStakedValue) => Number(vault.liquidity), 'desc')
        default:
          return vaults
      }
    }

    if (isActive) {
      chosenVaults = stakedOnly ? vaultsList(stakedOnlyVaults) : vaultsList(activeVaults)
    }
    if (isInactive) {
      chosenVaults = stakedOnly ? vaultsList(stakedInactiveVaults) : vaultsList(inactiveVaults)
    }
    if (isArchived) {
      chosenVaults = stakedOnly ? vaultsList(stakedArchivedVaults) : vaultsList(archivedVaults)
    }
    return sortVaults(chosenVaults)
  }, [
    sortOption,
    activeVaults,
    vaultsList,
    inactiveVaults,
    archivedVaults,
    isActive,
    isInactive,
    isArchived,
    stakedArchivedVaults,
    stakedInactiveVaults,
    stakedOnly,
    stakedOnlyVaults,
  ])

  const rowData = chosenVaultsMemoized.map((vault) => {
    const { tokenAddresses, quoteTokenAdresses } = vault
    const tokenAddress = tokenAddresses[2000]
    const quoteTokenAddress = quoteTokenAdresses[2000]
    const vaultContractAddress = vault.vaultAddresses[2000]
    const lpLabel = vault.lpSymbol && vault.lpSymbol.split(' ')[0].toUpperCase().replace('PANCAKE', '')

    const row: RowProps = {
      apr: {
        value: getDisplayApr(vault.apr),
        originalValue: vault.apr,
      },
      dailyApr: {
        value: getDisplayApr(vault.dailyApr),
        originalValue: vault.dailyApr,
      },
      vault: {
        label: lpLabel,
        pid: vault.pid,
        token: tokenAddress,
        quoteToken: quoteTokenAddress,
        vaultAddress: vaultContractAddress,
      },
      liquidity: {
        liquidity: vault.liquidity,
      },
      details: vault,
    }

    return row
  })

  const renderContent = (): JSX.Element => {
    if (viewMode === ViewMode.TABLE && rowData.length) {
      const columnSchema = DesktopVaultColumnSchema

      const columns = columnSchema.map((column) => ({
        id: column.id,
        name: column.name,
        label: column.label,
        sort: (a: RowType<RowProps>, b: RowType<RowProps>) => {
          switch (column.name) {
            case 'vault':
              return b.id - a.id
            case 'apr':
              if (a.original.apr.value && b.original.apr.value) {
                return Number(a.original.apr.value) - Number(b.original.apr.value)
              }

              return 0
            default:
              return 1
          }
        },
        sortable: column.sortable,
      }))
      return <Table data={rowData} columns={columns} userDataReady={userDataReady} />
    }
    return (
      <FlexLayout>
        <Route exact path={`${path}`}>
          {chosenVaultsMemoized.map((vault) => (
            <VaultCard
              key={vault.pid}
              vault={vault}
              displayApr={getDisplayApr(vault.apr)}
              cakePrice={new BigNumber(cakePrice)}
              account={account}
              removed={false}
            />
          ))}
        </Route>
        <Route exact path={`${path}/history`}>
          {chosenVaultsMemoized.map((vault) => (
            <VaultCard
              key={vault.pid}
              vault={vault}
              displayApr={getDisplayApr(vault.apr)}
              cakePrice={new BigNumber(cakePrice)}
              account={account}
              removed
            />
          ))}
        </Route>
        <Route exact path={`${path}/archived`}>
          {chosenVaultsMemoized.map((vault) => (
            <VaultCard
              key={vault.pid}
              vault={vault}
              displayApr={getDisplayApr(vault.apr)}
              cakePrice={new BigNumber(cakePrice)}
              account={account}
              removed
            />
          ))}
        </Route>
      </FlexLayout>
    )
  }

  const handleSortOptionChange = (option: OptionProps): void => {
    setSortOption(option.value)
  }

  return (
    <>
      <PageHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('Vaults')}
        </Heading>
        <Heading scale="lg" color="text">
          {t('Yield optimization for your favorite projects. Qu picante!')}
        </Heading>
      </PageHeader>
      <Page>
        <ControlContainer>
          <ViewControls>
            <ToggleWrapper>
              <Toggle
                id="staked-only-vaults"
                checked={stakedOnly}
                onChange={() => setStakedOnly(!stakedOnly)}
                scale="sm"
              />
              <Text> {t('Staked only')}</Text>
            </ToggleWrapper>
            <VaultTabButtons hasStakeInFinishedVaults={stakedInactiveVaults.length > 0} />
          </ViewControls>
          <FilterContainer>
            <LabelWrapper>
              <Text textTransform="uppercase">{t('Sort by')}</Text>
              <Select
                options={[
                  {
                    label: t('Hot'),
                    value: 'hot',
                  },
                  {
                    label: t('APR'),
                    value: 'apr',
                  },
                  {
                    label: t('Liquidity'),
                    value: 'liquidity',
                  },
                ]}
                onOptionChange={handleSortOptionChange}
              />
            </LabelWrapper>
            <LabelWrapper style={{ marginLeft: 16 }}>
              <Text textTransform="uppercase">{t('Search')}</Text>
              <SearchInput onChange={handleChangeQuery} placeholder="Search Vaults" />
            </LabelWrapper>
          </FilterContainer>
        </ControlContainer>
        {renderContent()}
        <StyledImage src="/images/decorations/3dpan.png" alt="Pancake illustration" width={120} height={103} />
      </Page>
    </>
  )
}

export default Vaults
