import React, { useState } from 'react'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import { Card, Flex, Text, Skeleton } from 'uikit'
import { getBscScanLink } from 'utils'
import { useTranslation } from 'contexts/Localization'
import ExpandableSectionButton from 'components/ExpandableSectionButton'
import { Vault } from 'state/types'
import { getAddress } from 'utils/addressHelpers'
import DetailsSection from './DetailsSection'
import CardHeading from './CardHeading'
import CardActionsContainer from './CardActionsContainer'
import ApyButton from './ApyButton'

export interface VaultWithStakedValue extends Vault {
  apr?: number
  dailyApr?: number
  liquidity?: BigNumber
}

const StyledCard = styled(Card)`
  align-self: baseline;
`

const VaultCardInnerContainer = styled(Flex)`
  flex-direction: column;
  justify-content: space-around;
  padding: 24px;
`

const ExpandingWrapper = styled.div`
  padding: 24px;
  border-top: 2px solid ${({ theme }) => theme.colors.cardBorder};
  overflow: hidden;
`

interface VaultCardProps {
  vault: VaultWithStakedValue
  displayApr: string
  removed: boolean
  cakePrice?: BigNumber
  account?: string
}

const VaultCard: React.FC<VaultCardProps> = ({ vault, displayApr, removed, cakePrice, account }) => {
  const { t } = useTranslation()

  const [showExpandableSection, setShowExpandableSection] = useState(false)

  const totalValueFormatted =
    vault.liquidity && vault.liquidity.isGreaterThanOrEqualTo(0)
      ? `$${vault.liquidity.toNumber().toLocaleString(undefined, { maximumFractionDigits: 0 })}`
      : ''

  const lpLabel = vault.lpSymbol && vault.lpSymbol.toUpperCase().replace('PANCAKE', '')

  const lpAddress = getAddress(vault.lpAddresses)
  const isPromotedVault = vault.tokenSymbol === 'CAKE'

  return (
    <StyledCard isActive={isPromotedVault}>
      <VaultCardInnerContainer>
        <CardHeading
          lpLabel={lpLabel}
          multiplier={vault.multiplier}
          token={vault.tokenSymbol}
          quoteToken={vault.quoteTokenSymbol}
        />
        {!removed && (
          <Flex justifyContent="space-between" alignItems="center">
            <Text>{t('APY')}:</Text>
            <Text bold style={{ display: 'flex', alignItems: 'center' }}>
              {vault.apr ? <ApyButton displayApr={displayApr} /> : <Skeleton height={24} width={80} />}
            </Text>
          </Flex>
        )}
        <Flex justifyContent="space-between">
          <Text>{t('Earn')}:</Text>
          <Text bold>MARIACHI</Text>
        </Flex>
        <CardActionsContainer vault={vault} lpLabel={lpLabel} account={account} cakePrice={cakePrice} />
      </VaultCardInnerContainer>

      <ExpandingWrapper>
        <ExpandableSectionButton
          onClick={() => setShowExpandableSection(!showExpandableSection)}
          expanded={showExpandableSection}
        />
        {showExpandableSection && (
          <DetailsSection
            removed={removed}
            bscScanAddress={getBscScanLink(lpAddress, 'address')}
            totalValueFormatted={totalValueFormatted}
            lpLabel={lpLabel}
          />
        )}
      </ExpandingWrapper>
    </StyledCard>
  )
}

export default VaultCard
