import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { VaultWithStakedValue } from 'views/Vaults/components/VaultCard/VaultCard'
import { useMatchBreakpoints } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import useDelayedUnmount from 'hooks/useDelayedUnmount'
import { useVaultUser } from 'state/hooks'

import Apr, { AprProps } from './Apr'
import Vault, { VaultProps } from './Vault'
import Details from './Details'
import Liquidity, { LiquidityProps } from './Liquidity'
import ActionPanel from './Actions/ActionPanel'
import CellLayout from './CellLayout'
import { DesktopVaultColumnSchema, MobileVaultColumnSchema } from '../types'

export interface RowProps {
  apr: AprProps
  dailyApr: AprProps
  vault: VaultProps
  liquidity: LiquidityProps
  details: VaultWithStakedValue
}

interface RowPropsWithLoading extends RowProps {
  userDataReady: boolean
}

const cells = {
  apr: Apr,
  dailyApr: Apr,
  vault: Vault,
  details: Details,
  liquidity: Liquidity,
}

const CellInner = styled.div`
  padding: 24px 0px;
  display: flex;
  width: 100%;
  align-items: center;
  padding-right: 8px;

  ${({ theme }) => theme.mediaQueries.xl} {
    padding-right: 32px;
  }
`

const StyledTr = styled.tr`
  cursor: pointer;
  border-bottom: 2px solid ${({ theme }) => theme.colors.cardBorder};
`

const AprMobileCell = styled.td`
  padding-top: 16px;
  padding-bottom: 24px;
`

const VaultMobileCell = styled.td`
  padding-top: 24px;
`

const Row: React.FunctionComponent<RowPropsWithLoading> = (props) => {
  const { details, userDataReady, apr, vault, liquidity, dailyApr } = props
  const hasStakedAmount = !!useVaultUser(details.pid).stakedBalance.toNumber()
  const [actionPanelExpanded, setActionPanelExpanded] = useState(hasStakedAmount)
  const shouldRenderChild = useDelayedUnmount(actionPanelExpanded, 300)
  const { t } = useTranslation()

  const toggleActionPanel = () => {
    setActionPanelExpanded(!actionPanelExpanded)
  }

  useEffect(() => {
    setActionPanelExpanded(hasStakedAmount)
  }, [hasStakedAmount])

  const { isDesktop, isMobile } = useMatchBreakpoints()

  const isSmallerScreen = !isDesktop
  const tableSchema = isSmallerScreen ? MobileVaultColumnSchema : DesktopVaultColumnSchema
  const columnNames = tableSchema.map((column) => column.name)
  const handleRenderRow = () => {
    const data = {
      liquidity,
      vault,
      apr,
      dailyApr,
      details,
    }
    if (!isMobile) {
      return (
        <StyledTr onClick={toggleActionPanel}>
          {Object.keys(props).map((key) => {
            const columnIndex = columnNames.indexOf(key)
            if (columnIndex === -1) {
              return null
            }

            switch (key) {
              case 'details':
                return (
                  <td key={key}>
                    <CellInner>
                      <CellLayout>
                        <Details actionPanelToggled={actionPanelExpanded} />
                      </CellLayout>
                    </CellInner>
                  </td>
                )
              case 'apr':
                return (
                  <td key={key}>
                    <CellInner>
                      <CellLayout label="APY">
                        <Apr {...apr} />
                      </CellLayout>
                    </CellInner>
                  </td>
                )
              case 'dailyApr':
                return (
                  <td key={key}>
                    <CellInner>
                      <CellLayout label="Daily APR">
                        <Apr {...dailyApr} />
                      </CellLayout>
                    </CellInner>
                  </td>
                )
              default:
                return (
                  <td key={key}>
                    <CellInner>
                      <CellLayout label={t(tableSchema[columnIndex].label)}>
                        {React.createElement(cells[key], { ...data[key], userDataReady })}
                      </CellLayout>
                    </CellInner>
                  </td>
                )
            }
          })}
        </StyledTr>
      )
    }
    return (
      <StyledTr onClick={toggleActionPanel}>
        <td>
          <tr>
            <VaultMobileCell>
              <CellLayout>
                <Vault {...vault} />
              </CellLayout>
            </VaultMobileCell>
          </tr>
          <tr>
            <AprMobileCell>
              <CellLayout label="APY">
                <Apr {...apr} />
              </CellLayout>
            </AprMobileCell>
            <AprMobileCell>
              <CellLayout label="Daily APR">
                <Apr {...dailyApr} />
              </CellLayout>
            </AprMobileCell>
          </tr>
        </td>
        <td>
          <CellInner>
            <CellLayout>
              <Details actionPanelToggled={actionPanelExpanded} />
            </CellLayout>
          </CellInner>
        </td>
      </StyledTr>
    )
  }

  return (
    <>
      {handleRenderRow()}
      {shouldRenderChild && (
        <tr>
          <td colSpan={6}>
            <ActionPanel {...props} expanded={actionPanelExpanded} />
          </td>
        </tr>
      )}
    </>
  )
}

export default Row
